import { API_KEY } from "./config.js";

// ĞŸĞ¾Ğ»ÑƒÑ‡ĞµĞ½Ğ¸Ğµ ĞºĞ¾Ğ¾Ñ€Ğ´Ğ¸Ğ½Ğ°Ñ‚ Ğ¿Ğ¾ Ğ½Ğ°Ğ·Ğ²Ğ°Ğ½Ğ¸Ñ Ğ³Ğ¾Ñ€Ğ¾Ğ´Ğ°
export async function getCoordinates(city) {
  const res = await fetch(
    `https://api.openweathermap.org/geo/1.0/direct?q=${encodeURIComponent(
      city
    )}&limit=1&appid=${API_KEY}`
  );
  const data = await res.json();

  if (!data.length) {
    throw new Error("Ğ“Ğ¾Ñ€Ğ¾Ğ´ Ğ½Ğµ Ğ½Ğ°Ğ¹Ğ´ĞµĞ½");
  }

  return {
    lat: data[0].lat,
    lon: data[0].lon,
    name: data[0].name,
  };
}

// ĞŸĞ¾Ğ»ÑƒÑ‡ĞµĞ½Ğ¸Ğµ Ğ½Ğ°Ğ·Ğ²Ğ°Ğ½Ğ¸Ñ Ğ³Ğ¾Ñ€Ğ¾Ğ´Ğ° Ğ¿Ğ¾ ĞºĞ¾Ğ¾Ñ€Ğ´Ğ¸Ğ½Ğ°Ñ‚Ğ°Ğ¼
export async function getCityName(lat, lon) {
  const res = await fetch(
    `https://api.openweathermap.org/geo/1.0/reverse?lat=${lat}&lon=${lon}&limit=1&appid=${API_KEY}`
  );
  const data = await res.json();

  return data[0]?.name || "ĞĞµĞ¸Ğ·Ğ²ĞµÑÑ‚Ğ½Ğ¾";
}

// ĞŸĞ¾Ğ»ÑƒÑ‡ĞµĞ½Ğ¸Ğµ Ğ¿Ğ¾Ğ»Ğ½Ğ¾Ğ¹ Ğ¸Ğ½Ñ„Ğ¾Ñ€Ğ¼Ğ°Ñ†Ğ¸Ğ¸ Ğ¾ Ğ¿Ğ¾Ğ³Ğ¾Ğ´Ğµ Ğ¿Ğ¾ ĞºĞ¾Ğ¾Ñ€Ğ´Ğ¸Ğ½Ğ°Ñ‚Ğ°Ğ¼
export async function fetchWeatherByCoords(lat, lon) {
  const [weatherData, airData, cityName] = await Promise.all([
    getWeatherData(lat, lon),
    getAirQuality(lat, lon),
    getCityName(lat, lon),
  ]);

  const current = weatherData.current;
  const today = weatherData.daily[0];

  return {
    city: cityName,
    temperature: Math.round(current.temp),
    feels_like: Math.round(current.feels_like),
    min: Math.round(today.temp.min),
    max: Math.round(today.temp.max),
    humidity: current.humidity,
    dew_point: current.dew_point,
    pressure: current.pressure,
    visibility: current.visibility,
    sunrise: current.sunrise,
    sunset: current.sunset,
    windSpeed: current.wind_speed,
    windGust: current.wind_gust || 0,
    windDeg: current.wind_deg,
    precipitation: today.rain || 0,
    timezoneOffset: weatherData.timezone_offset,
    daily: weatherData.daily,
    aqi: airData,
  };
}

// ĞŸĞ¾Ğ»ÑƒÑ‡ĞµĞ½Ğ¸Ğµ Ğ¿Ğ¾Ğ»Ğ½Ğ¾Ğ¹ Ğ¸Ğ½Ñ„Ğ¾Ñ€Ğ¼Ğ°Ñ†Ğ¸Ğ¸ Ğ¾ Ğ¿Ğ¾Ğ³Ğ¾Ğ´Ğµ Ğ¿Ğ¾ Ğ½Ğ°Ğ·Ğ²Ğ°Ğ½Ğ¸Ñ Ğ³Ğ¾Ñ€Ğ¾Ğ´Ğ°
export async function fetchWeather(cityName) {
  const { lat, lon } = await getCoordinates(cityName);
  return fetchWeatherByCoords(lat, lon);
}

// ĞŸĞ¾Ğ»ÑƒÑ‡ĞµĞ½Ğ¸Ğµ Ğ¿Ğ¾Ñ‡Ğ°ÑĞ¾Ğ²Ğ¾Ğ³Ğ¾ Ğ¿Ñ€Ğ¾Ğ³Ğ½Ğ¾Ğ·Ğ° Ğ¿Ğ¾Ğ³Ğ¾Ğ´Ñ‹
export async function fetchHourly(cityName) {
  const { lat, lon } = await getCoordinates(cityName);
  const data = await getWeatherData(lat, lon);

  return data.hourly.slice(0, 20).map((hour) => {
    return {
      time: new Date(hour.dt * 1000).toLocaleTimeString("ru-RU", {
        hour: "2-digit",
      }),
      temp: hour.temp,
      icon: hour.weather[0].icon,
    };
  });
}

// ĞŸĞ¾Ğ»ÑƒÑ‡ĞµĞ½Ğ¸Ğµ Ğ´Ğ°Ğ½Ğ½Ñ‹Ñ… Ğ¾ Ğ¿Ğ¾Ğ³Ğ¾Ğ´Ğµ (Ğ¾ÑĞ½Ğ¾Ğ²Ğ½Ğ¾Ğ¹ API)
async function getWeatherData(lat, lon) {
  const res = await fetch(
    `https://api.openweathermap.org/data/3.0/onecall?lat=${lat}&lon=${lon}&units=metric&exclude=minutely,alerts&lang=ru&appid=${API_KEY}`
  );
  return res.json();
}

// ĞŸĞ¾Ğ»ÑƒÑ‡ĞµĞ½Ğ¸Ğµ ĞºĞ°Ñ‡ĞµÑÑ‚Ğ²Ğ° Ğ²Ğ¾Ğ·Ğ´ÑƒÑ…Ğ°
async function getAirQuality(lat, lon) {
  const res = await fetch(
    `https://api.openweathermap.org/data/2.5/air_pollution?lat=${lat}&lon=${lon}&appid=${API_KEY}`
  );
  const data = await res.json();
  return data.list[0]?.main?.aqi || 1;
}
